body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* label {
  display: inline-block;
} */

label span {
  color: red
}

.form {
  border-radius: 5px;
  padding: 20px;
}

fieldset p {
  padding: 5px;
}

label{
  margin: 0 0 5px 0;
  width: 100%;
}

input {
  width: 100%; 
  padding: 4px;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  float: right;
  font-size: large;
  cursor: pointer;
}

button:disabled{
  background-color: rgb(172, 172, 172);
  cursor: no-drop;
}
.TodoItem {
    background: #32456a;
    padding: 5px;
    width: 200px;
    height: 150px;
    margin-top: 10px;
    margin-right: 10px;
    color: white;
    text-align: center;
    cursor: move;
    border: 2px solid #666666;
    border-radius: 10px;
    -ms-box-shadow: inset 0 0 3px #000;
    box-shadow: inset 0 0 3px #000;
}

.over {
  border: 2px #000;
}

.TodoItem.dragStart {
  opacity: 0.4;
}

.TodoClose {
  float: right;
  margin-right: 5px;
}

.TodoClose:hover {
  cursor: pointer;
}

.TodoList-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.TodoList-body {
  padding: 1%;
}

.TodoList-container {
  margin: auto;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
